@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
body, html {
	margin: 0;
	padding: 0;
	font-family: 'Roboto', sans-serif;
}
.App {
  background-color: #F9F9F9;
  /* display: flex; */
}


.Main {
    
}
.Sidebar {
	background-color: #887FDB;
	width: 20vw;
	height: 100vh;
	position: fixed;
}

.underline {
	left: 0;
	width: 6px;
	height: 65px;
	top: 265px;
	border-radius: 3px;
	background: #FFF;
	position: absolute;
	-webkit-transition: 0.5s ease;
}

.MenuItem {
	-webkit-transition: 0.5s ease;
}

.MenuItem:nth-of-type(1):hover ~ .underline {
	top: 140px;
}

.MenuItem:nth-of-type(2):hover ~ .underline {
	top: 202px;
}

.MenuItem:nth-of-type(4):hover ~ .underline {
	top: 332px;
}

.MenuItem:nth-of-type(5):hover ~ .underline {
	top: 390px;
}


.pages {
	
	margin-top: 1.5vh;
	padding: 0 2.5vw;
}

.line {
	width: 200px;
	height: 5px;
	border-top: 1px solid #6A5EC2;
	margin-left: 40px;
}

.Sidebar .SocialBar {
	margin-left: 30px;
}

.categories {
	margin-top: 1vh;
}


@media only screen and (max-height: 750px) {
	.Sidebar {
		overflow-y: scroll;
	}

	.underline {
		height: 0px;
	}
}

@media only screen and (max-width: 900px) {
	.Sidebar {
		width: 27vw;
	}
}

@media only screen and (max-width: 750px) {
	.Sidebar {
		width: 33vw;
	}
}
.MenuItem {
	color: white;
	display: flex;
	cursor: pointer;
}

.MenuItem p {
	font-size: 15px;
	margin-left: 29px;
}

.MenuItem img {
	width: 20px;
}
.Category {
	display: flex;
	align-items: center;
	/* margin-left: 36px; */
	margin: 3px 10px;
	padding-left: 26px;
	width: 100%;
	-webkit-transition: background-color 0.2s;
	transition: background-color 0.2s;
	cursor: pointer;
}

.category-circle {
	width: 7px;
	height: 8px;
	border-radius: 50%;
}

.Category p {
	font-size: 15px;
	color: #FFFFFF;
	letter-spacing: 1px;
	margin-left: 28px;
}

.Category:hover {
	
	background-color: rgb(89, 78, 173);
	border-radius: 5px;
	
}
.SocialBar {
	display: flex;
	margin: 0;
	flex-direction: column;
	justify-content: flex-end;
	margin-top: 30px;
}

.icons {
	display: flex;
}

.icons img {
	width: 18px;
	margin-left: 0;
	padding: 0;
	margin-right: 23px;
	transition: opacity 0.2s;
	-webkit-transition: opacity 0.2s;
	cursor: pointer;
	/*margin: 0 10px;*/
}

.icons img:hover {
	opacity: 0.8;
}

.SocialBar p {
	font-size: 15px;
	color: #FFFFFF;
	letter-spacing: 0;
	margin-left: 5px;
}
.Profile {
	height: 70px;
	background-color: #6A5EC2;
	display: flex;
	align-items: center;
	justify-content: center;
	color: white;
	margin: 0;
}

.profile-picture {
	width: 35px;
	height: 35px;
	border-radius: 50%;
	margin-left: 10px;
}

.arrow {
	width: 8px;
	margin-left: 12px;
	margin-right: 10px;
}

.profile-description {
	margin-left: 20px;
	display: flex;
	flex-direction: column;
	justify-content: center;
}
.Profile .name {
	font-size: 18px;
	padding: 0;
	margin: 0;
}

@media only screen and (max-width: 800px) {
	.profile-picture {
		height: 30px;
		width: 30px;
		margin-left: 10px;
	}

	.Profile .name {
		font-size: 16px;
	}

	.arrow {
		margin: 0 10px;
	}
}
.Dashboard {
	width: 80vw;
	margin-left: 20vw;
	border: 1px solid rgba(0, 0, 0, 0);
	min-height: 100vh;
}

.categories-toolbar {
	margin-top: 67px;
	margin-left: 10px;
}

@media only screen and (max-width: 900px) {
	.Dashboard {
		width: 73vw;
		margin-left: 27vw;
	}
}

@media only screen and (max-width: 750px) { 
	.Dashboard {
		width: 67vw;
		margin-left: 33vw;
	}

	.categories-toolbar {
		margin-top: 10px;
		
	}
}
.SearchBar {
	height: 67px;
	width: 100%;
	position: fixed;
	z-index: 3;
}

.SearchBar input {
	height: 100%;
	width: 100%;
	background: #FFFFFF;
	outline: none;
	border: none;
	box-shadow: 0 0 6px 2px rgba(0,0,0,0.15);
	background: url(data:image/png;base64,) 20px/20px no-repeat;
	background-color: #FFF;
	padding-left: 60px;
	font-weight: 500;
	font-size: 17px;
}


.Toolbar {
	height: 90px;
	width: 100%;
	display: flex;
	align-items: flex-end;
	justify-content: space-between;
}

.Toolbar .categories {
	padding: 0;
	margin: 0 0 10px 0;
	text-decoration: none;
	list-style: none;
	font-weight: 700;
	font-size: 18px;
	display: flex;
}

.categories button {
	border-radius: 4px;
	margin-left: 40px;
	padding: 10px 17px;
	-webkit-transition: all 0.25s ease;
	transition: all 0.25s ease;
	font-size: 16px;
	font-weight: 700;
	border: none;
	background-color: #F9F9F9;
}

button:focus {
	outline: none;
}

#selected {
	color: white;
	background-color: #887FDB;
}

/* .Toolbar .categories button:nth-of-type(1) {
	color: white;
	background-color: #887FDB;
} */

.Toolbar .categories button:hover {
	color: white;
	background-color: #887FDB;
}

.Toolbar .add-button {
	display: flex;
	font-size: 17px;
	color: #887FDB;
	align-items: center;
	cursor: pointer;
	outline: none;
	border: none;
	background: #F9F9F9;
	margin-right: 50px;
	margin-top: 5px;
	text-decoration: none;
}

.add-button img {
	width: 22px;
	margin-right: 20px;
}

@media only screen and (max-width: 925px) {
	.categories button {
		margin-left: 10px;
	}

	.add-button img {
		width: 18px;
		margin-right: 15px;
	}

	.Toolbar .add-button {
		font-size: 13px;
		margin-bottom: 5px;
		margin-right: 10px;
	}
}

@media only screen and (max-width: 750px) { 
	.Toolbar {
		height: 90px;
		margin-top: 80px;
		align-items: center;
	}
	
	.Toolbar .categories {
		width: 250px;
		flex-wrap: wrap;
		align-items: flex-start;
		margin-bottom: 0px;
	}

	.Toolbar .add-button {
		margin-right: 10px;
		padding: 0;
	}
}
.CardCollection {
	display: flex;
	flex-direction: row;
	justify-content: center;
	flex-wrap: wrap;
	/* margin-left: 30px; */
}
.Card {
	width: 175px;
	height: 175px;
	border-radius: 5px;
	background: #FFFFFF;
	box-shadow: 0 1px 8px 0 rgba(0,0,0,0.24);
	margin: 20px;
	padding: 16px 19px 35px 18px;
	-webkit-transition: 0.3s ease;
}

.Card:hover {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
	cursor: pointer;
}


.date {
	font-size: 16px;
	color: #848484;
	letter-spacing: 0;
	padding: 0;
	margin: 0;
}

.title-block {
	margin: 17px 0 0 0;
	padding: 0;
	display: flex;
	align-items: center;
}

.title {
	font-size: 18px;
	color: #333333;
	padding: 0;
	margin: 0 0 0 14px;
}

.category-circle {
	min-width: 9px;
	height: 9px;
	border-radius: 50%;
}

.description {
	font-size: 16px;
	color: #403D3D;
	letter-spacing: 0;
	line-height: 30px;
}
.Writer {
	width: 80vw;
	margin-left: 20vw;
	background-color: #F9F9F9;
	height: 100vh;
}

@media only screen and (max-width: 900px) {
	.Writer {
		width: 73vw;
		margin-left: 27vw;
	}
}


@media only screen and (max-width: 750px) {
	.Writer {
		width: 67vw;
		margin-left: 33vw;
	}
	
}

.TextEditor {
	display: flex;
	flex-direction: column;
}

.text-editor-contents {
	display: flex;
	flex-direction: column;
	margin-left: 10%;
	width: 80%;
	margin-top: 25px;
}

.title-area {
	border: none;
	outline: none;
	font-size: 45px;
	font-weight: 500;
	margin-bottom: 15px;
	background-color: #F9F9F9;
}

.TextEditor .line {
	width: 100%;
	margin: 0;
	border-top: 1px solid #BCBCBC;
}

.text-area{
	resize: none;
	height: 70vh;
	outline: none;
	border: none;
	font-size: 22px;
	font-family: 'Roboto', sans-serif;
	background-color: #F9F9F9;
	color: #484848;
	letter-spacing: 0.3px;
	line-height: 40px;
}


.close-btn {
	border: none;
	outline: none;
	background-color: #F9F9F9;
	width: 50px;
	height: 50px;
	cursor: pointer;
}

.close-link {
	width: 50px;
	height: 50px;
	margin-left: 30px;
	margin-top: 30px;
}

.close-btn img {
	width: 100%;
}


@media only screen and (max-width: 750px) {
	.title-area {
		font-size: 35px;
		margin-bottom: 10px;
	}
	.text-area {
		font-size: 18px;
		line-height: 35px;
	}
}
.Tags{
	height: 50px;
}
.AuthenticationCard {
    background: url(/violet-notes/static/media/purple-background.a4db6a52.jpg);
    background-size: cover;
    height: 100vh;
    display: flex;
    justify-content: center;
}


.outer-login {
    display: flex;
    justify-content: center;
    align-items: center;
}
.Login {
    max-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    background-color: white;
    width: 350px;
    border-radius: 18px;
    box-shadow: 0 2px 10px 6px rgba(0,0,0,0.09);
    padding: 5vh 2vw;

}

.Login .login-error-text {
    /* display: none; */
    margin-top: 10px;
    margin-bottom: -20px;
    padding: 0;
    font-size: 14px;
    color: #FF5050;
}

.Login .login-error {
    border: 1px solid #FF5050;
    background-color: #FFD9D9;
}


.Login input {
    width: 95%;
    height: 10vh;
    font-size: 17px;
    border: none;
    background-color: #F1F1F1;
    border-radius: 5px;
    padding-left: 10px;
    margin-top: 20px;
    outline: none;
}



/*Position CSS Classes*/

.top-right {
    margin: 0;
    display: flex;
    width: 100%;
    justify-content: flex-end;
    margin-bottom: 20px;
}

.bottom-center {
    margin-top: 20px;
}

.bottom-center Button {
    margin: 5px;
}

.bottom-input-link {
    margin: 10px;
}

.top-centered {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.top-centered Button {
    margin: 0 10px 20px 10px;
}


.Login h1 {
    margin: 0;
    margin-bottom: 10px;
    font-size: 30px;
    color: #484848;
}

.Login p {
    margin: 0;
    font-size: 16px;
    color: #484848;
    text-align: center;
    line-height: 25px;
}

.btn-link {
    font-weight: 700;
    font-size: 16px;
    background-color: white;
    border: none;
    outline: none;
    cursor: pointer;
    color: #484848;
}

.example-enter {
    opacity: 0.01
}

.example-enter.example-enter-active {
    opacity: 1;
    -webkit-transition: opacity 1s ease-in;
    transition: opacity 1s ease-in;
}

.example-leave {
    opacity: 1;
}

.example-leave.example-leave-active {
    opacity: 0.01;
    -webkit-transition: opacity 1s ease-in;
    transition: opacity 1s ease-in;
}


:root {
    --main-purple: #887FDB;
}

.Button {
    font-size: 15px;
    letter-spacing: 1.47px;
    width: 139px;
    height: 44px;
    border-radius: 22px;
    cursor: pointer;
    outline: none;
}

.withColor {
    color: #FFF;
    background-color: #887FDB;
    background-color: var(--main-purple);
    border: none;
    -webkit-transition: all 0.1s;
    transition: all 0.1s;
}

.withColor:hover {
    color: #887FDB;
    color: var(--main-purple);
    background-color: #FFF;
    border: 1px solid #887FDB;
    border: 1px solid var(--main-purple);
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
}

.plain {
    color: #887FDB;
    color: var(--main-purple);
    background-color: #FFF;
    border: 1px solid #887FDB;
    border: 1px solid var(--main-purple);
}

.plain:hover {
    color: #FFF;
    background-color: #887FDB;
    background-color: var(--main-purple);
    border: none;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
}
